import React from 'react';
import TestCube from '../../images/cube.svg'
import './BlockUI.css';

const BlockUI = ({name, buildBlocks,update}) => {
  return (<div className="blockUI-root">
              <ul className="blockUI-data">
                <li className="block-UI">
                  <img src={TestCube} alt="ImageIcon"/></li>
                <li className="block-UI"><p>{name}</p></li>
                <li><button onClick={()=>{update(null)}}>^</button></li>
              </ul>
              <div className="blockUI-build">
                {buildBlocks.map(element => {
                  return (<div className="block-buildRoot" key={`Field_${element}`} onClick={()=>{console.log(element)}}>
                            <div>
                              <p><span>âœ¯</span>5</p>
                              <img src={TestCube} alt=""/>
                            </div>
                            <p>{element}</p>
                          </div>)
                })}
              </div>
            </div>);
}

export default BlockUI;