import { combineReducers } from 'redux';
import {  GAME_LOADING_PENDING,GAME_LOADING_ERROR,GAME_LOADING_FINISH, WORLD_LOADING_ERROR, WORLD_LOADING_FINISH, WORLD_LOADING_PENDING, UI_BLOCK_TYPE} from './constants';

const intialLoadingState = {
    isPending: false,
    error: "",
    models: {},
    config: []
}

const worldLoadingintial = {
    isLoading: false,
    error: ""
}

const gameUIintial = {
    ui: null
}

export const uiUpdate = (state=gameUIintial,action={})=>{
    switch (action.type) {
        case UI_BLOCK_TYPE: 
            return Object.assign({},state,{ui: action.payload});
        default:
            return state;
    }
}

export const worldLoading = (state=worldLoadingintial, action={})=>{
    switch (action.type) {
        case WORLD_LOADING_ERROR:
            return Object.assign({},state,{error: action.payload, isLoading: false});
            case WORLD_LOADING_PENDING:
                return Object.assign({},state,{isLoading: true});
            case WORLD_LOADING_FINISH:
                return Object.assign({},state,{isLoading: false})
        default:
            return state;
    }
}

export const gameLoading = (state=intialLoadingState, action={})=>{
    switch (action.type) {
        case GAME_LOADING_ERROR:
            return Object.assign({},state,{error: action.payload, isPending: false});
        case GAME_LOADING_PENDING:

            return Object.assign({},state,{isPending: true});
        case GAME_LOADING_FINISH:
            return Object.assign({},state,{isPending: false, models: action.payload.models, config: action.payload.config})
    
        default:
            return state;
    }
}






export const rootReducer = combineReducers({gameLoading, worldLoading, uiUpdate});
