import React, {Component} from 'react';
import * as THREE from 'three';
import {connect} from 'react-redux';
import CameraControls from 'camera-controls';
import { BloomEffect, EffectComposer, EffectPass, RenderPass } from "postprocessing";
import {loadingGameContent,  worldLoading,uiUpdate} from '../../redux/actions'
import WorldGeneration_V2 from '../../components/WorldGeneration/WorldGeneration_v2'
import BlockUI from '../../components/BlockUI/BlockUI';
import './App.css';

const mapStateToProps = state => {return {
  error: state.gameLoading.error,
  isPending: state.gameLoading.isPending,
  models: state.gameLoading.models,
  config: state.gameLoading.config,
  isLoading: state.worldLoading.isLoading,
  ui: state.uiUpdate.ui
}};
const mapDispatchToProps = dispatch =>{
  return {
     loadGameAssets: ()=>dispatch(loadingGameContent()),
     changeWorldState:()=>dispatch(worldLoading()),
     updateUI: element=>dispatch(uiUpdate(element))
  }
}


class App extends Component{
  componentDidMount(){
    
    // THREE Scene
   this.scene = new THREE.Scene();
   this.props.loadGameAssets();
   CameraControls.install( { THREE: THREE } );

  // renderer and Postprossing 
   this.renderer = new THREE.WebGLRenderer({antialias: true});
   this.composer = new EffectComposer(this.renderer);
   this.renderer.shadowMap.enabled = true;
   this.renderer.autoClear = false
   this.renderer.setClearColor('#000000');
   this.renderer.setSize(window.innerWidth,window.innerHeight);
   this.renderer.setPixelRatio( window.devicePixelRatio );

   // click methods
   this.raycaster = new THREE.Raycaster();
   // use orthographic Camera?
   this.camera = new THREE.PerspectiveCamera(75,window.innerWidth/window.innerHeight,0.1,1000);
   this.camera.rotation.set(0,0,0);
   this.camera.position.set(1,1,1);
  

  
   //this.composer.addPass(new RenderPass(this.scene,this.camera));
 
   this.clock = new THREE.Clock();
   this.cameraControls = new CameraControls( this.camera, this.renderer.domElement );

   this.mount.appendChild(this.renderer.domElement);
   window.addEventListener('resize',this.handleResize);
   window.addEventListener('click',this.blockClicked)
   window.addEventListener('keydown',this.createWorld)
   const directionalLight = new THREE.DirectionalLight();
   directionalLight.position.setY(200);
   this.scene.add(directionalLight);
  
   const hemisphereLight = new THREE.HemisphereLight();
   this.scene.add(hemisphereLight);
   this.start()
  }

  componentDidUpdate(){
  }
  componentWillUnmount(){
    this.stop();
    this.mount.removeChild(this.renderer.domElement);
  }
  start = () => {
    if(!this.frameId){
      this.frameId = requestAnimationFrame(this.animate)
    }
  } 
  stop = () =>{
    cancelAnimationFrame(this.frameId);
  }
  animate = () =>{
    this.renderScene();
    this.frameId = window.requestAnimationFrame(this.animate);
  }
  renderScene = () => {
    const delta = this.clock.getDelta();
    const hasControlsUpdated = this.cameraControls.update( delta );
    this.renderer.render(this.scene,this.camera);
    this.composer.render(delta);
  }
  
  createWorld = () =>{
    if(!this.props.isPending){
      const world_v2 = new WorldGeneration_V2({
                                            config: this.props.config,
                                            models: this.props.models,
                                            scene: this.scene,
                                            worldSize: 11,
                                            factions: ["Xin-xi","Imperius"],
                                            state: [this.props.changeWorldState]
                                              });
      world_v2.genWorld();
  }
 }

 

  // window Listeners
  handleResize = (e) => {
    this.renderer.setSize(window.innerWidth,window.innerHeight);
    this.renderer.setPixelRatio( window.devicePixelRatio );
    this.camera.aspect = window.innerWidth/window.innerHeight;
    this.camera.updateProjectionMatrix();
  }
  blockClicked = ev =>{
    const mouse3D = new THREE.Vector3((ev.clientX/window.innerWidth)*2-1,-(ev.clientY/window.innerHeight)*2+1,0.5);
    mouse3D.unproject(this.camera);
    this.raycaster.set(this.camera.position,mouse3D.sub(this.camera.position).normalize());
    const intersects = this.raycaster.intersectObjects(this.scene.children);
    const userSlector  = this.scene.children.find(item=>{
       return item.userData.user === "Imperius"//this will be a redux State
    });
    
    if(intersects.length > 0){
    const data = intersects[0].object;
    const clicked = data.position;
    userSlector.position.set(clicked.x,clicked.y,clicked.z);
        this.props.updateUI(<BlockUI update={this.props.updateUI} name={data.userData.type} buildBlocks={data.userData.build}/>);
    }else{
      userSlector.position.set(0,-1,0);
      this.props.updateUI(null);
    }
  
  }

  render(){ 
    return (
       <div id="three-scene" ref={mount => {this.mount = mount}}>
         {this.props.ui}
       </div>
    );
    }

}
export default connect(mapStateToProps, mapDispatchToProps)(App);



