import SimplexNoise from 'simplex-noise';
import CREATE from '../Create/index';

/**
 * Credit 
 *   https://www.redblobgames.com/maps/terrain-from-noise/
 * 
 * 
 * 
 * 
 * 
 */
export default class WorldGeneration_V2 {
    constructor(config){
        this.config = config;
        this.models = new CREATE(config.models);
        this.genNoise = new SimplexNoise();
    }
    
    noise(nx, ny) {
        return this.genNoise.noise2D(nx, ny) / 2 + 0.5;  // Rescale from -1.0:+1.0 to 0.0:1.0
    }

    createWorldSeed = (height,width)=>{
        let value = [];   
        for (let y = 0; y < height; y++) {
          value[y] = [];
          for (let x = 0; x < width; x++) {      
            let nx = x/width - 0.5, ny = y/height - 0.5;
            value[y][x] = this.noise(nx, ny);
          }
        }
        return value;
      }

    genWorld = () =>{
        const {Slector} = this.models;
        const {worldSize,factions} = this.config;
        this.worldSeedArray = [];
        this.worldArray = [];
        this.coordZ = 0;
        this.coordX = 0;
        this.row = 0;
        this.colum = 0;
        let seed = this.createWorldSeed(worldSize, worldSize);
       
        seed.forEach(array=>{
            array.forEach(int=>{
               this.worldSeedArray.push(this.intToBlock(int));
            });
        });
        this.worldSeedArray.forEach(type=>{
            this.addToWorld(type,this.models);
            this.row++
            this.coordZ += 0.5;
            if(this.row > (worldSize-1)){
                this.colum++
                if(this.colum < worldSize){
                 this.row = 0;
                 this.coordZ = 0;
                 this.coordX += 0.5;
                }
            }
        })


           // add Slector Boxs
           for(let i = 0; i < factions.length; i++){
             this.config.scene.add(Slector(factions[i],-(i+1)));
           }
           
           this.addToScene(this.worldArray)
           console.log(this.config.scene)
       }

       addToWorld = (type,{Field,Ocean,Water}) =>{
        switch (type) {
            case "OCEAN":
                this.worldArray.push(Ocean({x:this.coordX,y:0,z:this.coordZ},null));
               return;
            case "FIELD":
                this.worldArray.push(Field({x:this.coordX,y:0,z:this.coordZ},null));
                return;
            case "WATER":
                this.worldArray.push(Water({x:this.coordX,y:0,z:this.coordZ},null));
                return;
            default:
                return;
        }
       }
   
       addToScene = array =>{
        array.forEach(item=>{
          this.config.scene.add(item);
        });
      }

      intToBlock = int =>{
         if(int <= 0.3){
             return "OCEAN"
         }else if(int < 0.6 && int > 0.3){
            return "WATER"
         }else{
             return "FIELD"
         }
      }

      deleteWorld = (worldSize) => {
         
          for(let i = 2; i >= (worldSize+2); i++ ){
            this.config.scene.children[i].mesh.geometry.dispose();
            delete this.config.scene.children[i];
          }
      }

}
