import { Mesh } from 'three';

export default class CREATE {
    constructor(models){
        this.models = models;
    }


    Cloud = ({x, y, z}) =>{
        const { cloud } = this.models;
        let mesh = new Mesh(cloud.geometry, cloud.material);
        mesh.name = "cloud"
        mesh.userData = {
          type: "Cloud",
          faction : 'none'
        }
        mesh.position.set(x,y,z)
        return mesh;
      }
      Water = ({x, y, z}, faction = null) =>{
        const { water } = this.models;
        let mesh = new Mesh(water.geometry, water.material);
        mesh.name = "water"
        mesh.userData = {
          type: "Water",
          faction : faction,
          attached: [],
          build:["Port","Water Temple"]
        }
        mesh.position.set(x,y,z)
        return mesh;
      }
      Field = ({x, y, z}, faction = null) =>{
        const { field } = this.models;
        let mesh = new Mesh(field.geometry, field.material);
        mesh.addEventListener("click",function(ev){console.log("You clicked a Field Block")})
        mesh.name = "base_field"
        mesh.userData = {
          type:"Field",
          faction : faction,
          attached: [],
          build: ["Road","Farm","Sawmill","GrowForest"]
        }
        mesh.position.set(x,y,z)
        return mesh;
      }
      Ocean = ({x, y, z}, faction = null) =>{
        const { ocean } = this.models;
        let mesh = new Mesh(ocean.geometry, ocean.material);
        mesh.name = "ocean"
        mesh.userData = {
          type:"Ocean",
          faction : faction,
          attached: [],
          build: ["Water Temple"]
        }
        mesh.position.set(x,y,z)
        return mesh;
      }
      
    Fruit = ({x, y, z},faction) =>{
        const { fruit } = this.models;
        let mesh = new Mesh(fruit.geometry, fruit.material);
        mesh.name = "fruit_typeOne"
        mesh.userData = {
          type:"Fruit",
          faction : faction
        }
        mesh.position.set(x,y,z)
        return mesh;
      }
    Slector = (user,y) =>{
      const {slector} = this.models;
      let mesh = new Mesh(slector.geometry,slector.material);
      mesh.name="slector";
      mesh.userData={
        type:"Slector",
        user:user
      }
      mesh.position.set(0,y,0);
      return mesh;
    }
    }
