import {GAME_LOADING_FINISH,GAME_LOADING_PENDING, GAME_LOADING_ERROR, WORLD_LOADING_ERROR, WORLD_LOADING_FINISH, WORLD_LOADING_PENDING, UI_BLOCK_TYPE} from './constants';
import {LoadingManager, FileLoader} from 'three';
import {MTLLoader, OBJLoader} from 'three-obj-mtl-loader'


export const uiUpdate = (element) => dispatch =>{
  dispatch({type: UI_BLOCK_TYPE, payload: element});
}

export const worldLoading = (state,error) => dispatch =>{
  switch (state.toLowercase()) {
    case "loading":
      dispatch({type: WORLD_LOADING_PENDING});
      break;
    case "error": 
      dispatch({type: WORLD_LOADING_ERROR, payload: error});
      break;
    case "finish": 
      dispatch({type: WORLD_LOADING_FINISH});
      break;
    default:
      break;
  }
}

export const loadingGameContent = () => dispatch =>{
    let models = {},config = [], modelPath = "./models/"
    const manager = new LoadingManager();
    manager.onStart =  () =>{ dispatch({type: GAME_LOADING_PENDING}); };
    manager.onLoad =  ( ) => {  dispatch({type: GAME_LOADING_FINISH, payload: { models, config }    });};
    manager.onError = err => { dispatch({type: GAME_LOADING_ERROR, payload: err});};
    manager.onProgress =  ( url, itemsLoaded, itemsTotal ) => { console.log( `Loading file: ${url} .\n Loaded ${itemsLoaded} of ${itemsTotal} files.` ); };
    
    const configLoader = new FileLoader(manager).setPath('./config/').setResponseType('json');
   
    configLoader.load('config.json', json=>{
         config.push(json);
    });
    configLoader.load('mods.json',json=>{
        config.push(json);
    })
    const mtlLoader = new MTLLoader(manager).setPath(modelPath);
    
      mtlLoader.load('field.mtl',materials=>{
        materials.preload();
        const objLoader = new OBJLoader(manager).setPath(modelPath).setMaterials(materials);
        objLoader.load('field.obj',object=>{
         models.field = {
           geometry: object.children[0].geometry,
           material: object.children[0].material
         }
        });
      });

      mtlLoader.load('cloud.mtl',materials=>{
        materials.preload();
        const objLoader = new OBJLoader(manager).setPath(modelPath).setMaterials(materials)
        objLoader.load('cloud.obj',object=>{
         models.cloud = {
           geometry: object.children[0].geometry,
           material: object.children[0].material
         }
        });
      });

      mtlLoader.load('water.mtl',materials=>{
        materials.preload();
        const objLoader = new OBJLoader(manager).setPath(modelPath).setMaterials(materials)
        objLoader.load('water.obj',object=>{
         models.water = {
           geometry: object.children[0].geometry,
           material: object.children[0].material
         }
        });
      });

      mtlLoader.load('slector.mtl',materials=>{
        materials.preload();
        const objLoader = new OBJLoader(manager).setPath(modelPath).setMaterials(materials)
        objLoader.load('slector.obj',object=>{
         models.slector = {
           geometry: object.children[0].geometry,
           material: object.children[0].material
         }
        });
      });

      mtlLoader.load('ocean.mtl',materials=>{
        materials.preload();
        const objLoader = new OBJLoader(manager).setPath(modelPath).setMaterials(materials)
        objLoader.load('ocean.obj',object=>{
         models.ocean = {
           geometry: object.children[0].geometry,
           material: object.children[0].material
         }
        });
      });
      mtlLoader.load('fruit_t_one.mtl',materials=>{
        materials.preload();
        const objLoader = new OBJLoader(manager).setPath(modelPath).setMaterials(materials)
        objLoader.load('fruit_t_one.obj',object=>{
         models.fruit = {
           geometry: object.children[0].geometry,
           material: object.children[0].material
         }
        });
      });

   
}


